@import url(https://fonts.googleapis.com/css?family=Roboto:500);
.WelcomeScreen {
	position: fixed;
	width: 100%;
	height: 100%;
	top: 0;
	background: white;
}

.google-btn {
	width: 184px;
	height: 42px;
	margin: 1rem;
	background-color: #4285f4;
	border-radius: 2px;
	box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}

.google-btn:hover {
	cursor: pointer;
	box-shadow: 0 0 6px #4285f4;
}

.google-btn:active {
	background: #1669f2;
}

.google-icon-wrapper {
	position: absolute;
	margin-top: 1px;
	margin-left: 1px;
	width: 40px;
	height: 40px;
	border-radius: 2px;
	background-color: #fff;
}

.google-icon {
	position: absolute;
	margin-top: 11px;
	margin-left: -8px;
	width: 18px;
	height: 18px;
}

.google-btn-text {
	cursor: pointer;
	float: right;
	color: #fff;
	font-size: 14px;
	letter-spacing: 0.2px;
	font-family: 'Roboto';
	background-color: #4285f4;
	width: 80%;
	height: 100%;
	text-align: center;
	border: none;
}

/* Make clicks pass-through */
#nprogress {
	pointer-events: none;
}

#nprogress .bar {
	background: #29d;

	position: fixed;
	z-index: 1031;
	top: 0;
	left: 0;

	width: 100%;
	height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
	display: block;
	position: absolute;
	right: 0px;
	width: 100px;
	height: 100%;
	box-shadow: 0 0 10px #29d, 0 0 5px #29d;
	opacity: 1;

	-webkit-transform: rotate(3deg) translate(0px, -4px);
	transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
	display: block;
	position: fixed;
	z-index: 1031;
	top: 15px;
	right: 15px;
}

#nprogress .spinner-icon {
	width: 18px;
	height: 18px;
	box-sizing: border-box;

	border: solid 2px transparent;
	border-top-color: #29d;
	border-left-color: #29d;
	border-radius: 50%;

	-webkit-animation: nprogress-spinner 400ms linear infinite;
	animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
	overflow: hidden;
	position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
	position: absolute;
}

@-webkit-keyframes nprogress-spinner {
	0% {
		-webkit-transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
	}
}
@keyframes nprogress-spinner {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

/***************************/
/********* GENERAL *********/
/***************************/

ul {
	padding: 0;
	list-style: none;
}

p {
	margin: 5px 0;
}

input[type='text'],
input[type='number'] {
	width: 100%;
	padding: 10px;
	border-radius: 5px;
	outline: none;
	border: 1px solid #ccc;
	font-size: 15px;
	line-height: 20px;
	align-self: center;
	height: 40px;
}

body.modal {
	overflow: hidden;
}

/***************************/
/*********** APP ***********/
/***************************/

#App {
	text-align: center;
}

#App * {
	box-sizing: border-box;
}

#App > .Alert {
	background-color: black;
	margin: 0;
	padding: 10px;
}

/***************************/
/********* HEADER **********/
/***************************/

#Header{
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 20px;
	border-bottom: 1px solid #ccc;
	background-color: #eee;
	box-shadow: 0px 0px 15px -5px rgba(0, 0, 0, 0.25);
}

#Meet{
	position: relative;
	font-size: 20px;
	font-weight: bold;
	letter-spacing: 5px;
}

#Meet::after{
	position: absolute;
	content: '®';
}

@media only screen and (max-width: 680px) {
	#Header{
		display: grid;
		justify-content: center;
		grid-gap: 20px;
		gap: 20px;
	}	
}

/***************************/
/******* CITY SEARCH *******/
/***************************/

#CitySearch {
	position: relative;
	display: flex;
	flex-direction: column;
	max-width: 300px;
}

#CitySearch .city {
	width: 250px;
}

#CitySearch .suggestions {
	border-radius: 5px;
	overflow: hidden;
	width: 250px;
	position: absolute;
	z-index: 2;
	align-self: center;
	margin: 0;
	top: 40px;
	font-size: 15px;
	box-shadow: 0px 0px 10px -5px rgba(0, 0, 0, 0.75);
}

#CitySearch .suggestions > li {
	cursor: pointer;
	text-align: left;
	padding: 10px;
	background-color: #eee;
}

#CitySearch .suggestions > li:last-child {
	border-bottom-left-radius: 5px;
	border-bottom-right-radius: 5px;
}

#CitySearch .suggestions > li:hover {
	background-color: #bcdcdb;
}

/***************************/
/***** NUMBER OF EVENTS ****/
/***************************/

#NumberOfEvents {
	position: relative;
	max-width: 100px;
}

/***************************/
/********** ALERT **********/
/***************************/

#CitySearch .Alert{
	position: absolute;
	top: 0;
	left: 0;
	font-size: 10px;
	width: -webkit-max-content;
	width: max-content;
	-webkit-transform: translateY(-100%);
	        transform: translateY(-100%);
}

#NumberOfEvents .Alert{
	position: absolute;
	bottom: 0;
	right: 0;
	font-size: 10px;
	width: -webkit-max-content;
	width: max-content;
	-webkit-transform: translateY(100%);
	        transform: translateY(100%);
}

@media only screen and (max-width: 680px) {
	#NumberOfEvents .Alert{
		left: 0;
	}	
}

/***************************/
/******** EVENT LIST *******/
/***************************/

#EventList {
	display: grid;
	grid-template-columns: 1fr;
	grid-gap: 20px;
	gap: 20px;
	margin: 20px;
}

@media only screen and (min-width: 980px) {
	#EventList{
		grid-template-columns: 1fr 1fr;
	}	
}

/***************************/
/********** EVENT **********/
/***************************/

.Event {
	padding: 10px 10px 35px;
	text-align: left;
	border: 1px solid #ccc;
	border-radius: 4px;
	position: relative;
	box-shadow: 0px 0px 15px -5px rgba(0, 0, 0, 0.25);
}

.Event .overview div:not(:last-child) {
	margin-bottom: 10px;
}

.Event .overview div.title {
	font-size: 25px;
	font-weight: bold;
	margin-bottom: 15px;
}

.Event button {
	position: absolute;
	right: 10px;
	bottom: 10px;
	padding: 15px;
	border-radius: 5px;
	border: none;
	text-transform: uppercase;
	text-decoration: none;
	outline: none;
	cursor: pointer;
	font-size: 12.5px;
	font-weight: bold;
	background-color: #eee;
}

.Event button:hover{
	background-color: #bcdcdb;
}

/***************************/
/********** MODAL **********/
/***************************/

#Modal {
	position: absolute;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100vw;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #eeeeee80;
}

#Modal #content {
	position: relative;
	padding: 40px 40px;
	width: 50%;
	background-color: white;
}

#Modal #content div:not(:last-child) {
	margin-bottom: 10px;
}

#Modal #content #title{
	font-size: 25px;
	font-weight: bold;
	margin-bottom: 15px;
}

#Modal #content a{
	display: inline-block;
	float: right;
	margin-top: 25px;
	color: black;
	padding: 15px;
	border-radius: 5px;
	border: none;
	text-transform: uppercase;
	text-decoration: none;
	outline: none;
	cursor: pointer;
	font-size: 12.5px;
	font-weight: bold;
	background-color: #eee;
}

#Modal #content a:hover{
	background-color: #bcdcdb;
}

#Modal #content #close{
	cursor: pointer;
	position: absolute;
	top: 20px;
	right: 20px;
	margin: 0;
	width: 20px;
	height: 20px;
}

#Modal #content #close::before,
#Modal #content #close::after{
	content: '';
	position: absolute;
	left: 10px;
	width: 2px;
	height: 20px;
	background-color: black;
}

#Modal #content #close::before {
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}

#Modal #content #close::after{
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}

body {
	margin: 0;
	font-family: -apple-system, 'Roboto', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

